import React from 'react';
import './MyProjects.css';
import gradient from './gradient.png';
import robots from './robots.png';
import facerecognition from './facerecognition.png';

class MyProjects extends React.Component {
    
    closeProjects = () =>{
        this.props.settingProjectsState(false);
    }
    nextArrow() {
        const img1 = document.getElementsByClassName('first')[0];
        const img2 = document.getElementsByClassName('second')[0];
        const img3 = document.getElementsByClassName('third')[0];
        if (window.innerWidth<=750) {
            if(img3.style.display==='' || img3.style.display==='none'){
                img1.style.display='none';
                img2.style.display='none';
                img3.style.cssText='display: block;animation:bigger 1s;';
            }else if(img2.style.display==='' || img2.style.display==='none'){
                img1.style.display='none';
                img3.style.display='none';
                img2.style.cssText='display: block;animation:slideup 1s;';
            }
        }
        if (window.innerWidth<=1050 && window.innerWidth>750) {
            if (img3.style.display==='' || img3.style.display==='none')  {
            img1.style.display='none';
            img3.style.cssText='display: block;animation:bigger 1s;';
        }
    }
    }
    prevArrow() {
        const img1 = document.getElementsByClassName('first')[0];
        const img2 = document.getElementsByClassName('second')[0];
        const img3 = document.getElementsByClassName('third')[0];
        if (window.innerWidth<=750) {
         if(img2.style.display==='' || img2.style.display==='none'){
            img1.style.display='none';
            img3.style.display='none';
            img2.style.cssText='display: block;animation:slideup 1s;';
        }else if(img1.style.display==='' || img1.style.display==='none'){
            img2.style.display='none';
            img3.style.display='none';
            img1.style.cssText='display: block;animation:bigger 1s;';
        }
        }
        if (window.innerWidth<=1050 && window.innerWidth>750) {
             if (img1.style.display==='' || img1.style.display==='none')  {
            console.log(img1.style.display);
            img3.style.display='none';
            img1.style.cssText='display: block;animation:slideup 1s;';
        }
    }
    }

    render () {
        return (
        <div className='projectsBlock'>
            <div className='projectsSlides'>
            <h2>My Projects On GitHub:</h2>
            <i className="fas fa-caret-left" id='left' onClick={this.prevArrow}></i>
            <div className='carousel'>
            <div className='first imgFrame'>
            <a href='https://github.com/Sloor/backgroundGenerator/tree/master/gradient' target='_blank' rel="noopener noreferrer">
            <img className='carousel' src={gradient} alt='gradientProj'/>
                <div className='textover'>
                <p>A website for trying different color gradients for designing purposes</p>
                <i className="fas fa-external-link-alt"></i>
                </div>
            </a>
            </div>
            <div className='second imgFrame'>
            <a href='https://sloor.github.io/robotsearch/' target='_blank' rel="noopener noreferrer"><img className='carousel' src={robots} alt='robotsProj'/>
                <div className='textover'>
                <p>A simple page demonstrating searching using AJAX</p>
                <i className="fas fa-external-link-alt"></i>
                </div>
            </a>
            </div>
            <div className='third imgFrame'>
            <a href='https://sloor.github.io/facerecognition/' target='_blank' rel="noopener noreferrer"><img className='carousel' src={facerecognition} alt='todoProj'/>
                <div className='textover'>
                <p>A face recognition applicaiton that accepts a URL input</p>
                <i className="fas fa-external-link-alt"></i>
                </div>
            </a>
            </div>
            </div>
            <i className="fas fa-caret-right" id='right' onClick={this.nextArrow}></i>
            </div>
            <div onClick={this.closeProjects} className='projectsLayer'></div>
        </div>
        );   
    }
}

export default MyProjects;